function ContactSection(props) {
  let { mail_icon } = props;
  let [email, setEmail] = useState("");
  let [msg, setMsg] = useState("");

  function post() {
    debugger;
    axios
      .post("//xl1p1-z?", JSON.stringify({ email: email, msg: msg }), {
        validateStatus: true,
        timeout: 3000,
      })
      .then(
        (resp) => {
          if (resp.status === 200) {
            setEmail("");
            setMsg("");
          }
        },
        (err) => {}
      );
  }

  return (
    <div className="row py-4 " style={{ backgroundColor: "" }}>
      <div className=" col-12 col-md-6 border-dark border-right">
        <div className="col-12 offset-md-2 col-md-8">
          <div className="form-group">
            <label for="e-mail">e-mail</label>
            <input
              value={email}
              onChange={(e) => {
                setEmail(e.target.value);
              }}
              type="text"
              id="e-mail"
              className="form-control"
            />
          </div>
          <div className="col-12"></div>
          <div className=" form-group">
            <label for="text-area">Message</label>
            <textarea
              className="form-control"
              style={{ height: "200px" }}
              value={msg}
              onChange={(e) => {
                setMsg(e.target.value);
              }}
            ></textarea>
          </div>
          <div className="col-12"></div>
          <div className="col-12 ">
            <input
              type="button"
              className="btn btn-success form-control"
              value="Send"
              onClick={() => post()}
            ></input>
          </div>
        </div>
      </div>

      <div className=" col-12 col-md-6">
        <img className="" src={mail_icon}></img>
        <h2 className="text-warning "> Drop a line.</h2>
        <h3> Any time.</h3>
        <h1>And, I'll get back to you.</h1>
        <h2>I always do.</h2>
      </div>
    </div>
  );
}
